import React from 'react';
import PlayButton from '@material-ui/icons/PlayArrow';
import PauseButton from '@material-ui/icons/Pause';
import { MuiThemeProvider, AppBar, Tabs, Tab, Icon, IconButton, Toolbar } from '@material-ui/core';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Theme from './Theme';
import Slideshow from './components/Slideshow';
import { BasePage } from './components/BasePage';
import Eventos from './components/Eventos/Eventos';
import Diagnostico from './components/Diagnostico/Diagnostico';
import Retro from './components/Retro/Retro';
import history from 'history/createBrowserHistory';
import Ritos from './components/Ritos/Ritos';
import OneOnOne from './components/OneOnOne/OneOnOne';

class App extends React.Component {
  state = {
    tabs: 0,
    play: true
  };

  componentDidMount() {
    this.timer = setInterval(() => {
      this.setState({
        tabs: (this.state.tabs + 1) % 5
      });
    }, 15 * 1000);
  }

  handleChange = (e, value) => {
    this.setState({ tabs: value });
  };

  handleClick = () => {
    this.setState({ play: !this.state.play });
  };

  render() {
    const { tabs } = this.state;
    return (
      <MuiThemeProvider theme={Theme}>
        <AppBar position="static">
          <Tabs value={tabs} onChange={this.handleChange} style={{ flexGrow: 1 }}>
            <Tab label="Ritos" />
            <Tab label="Diagnóstico" />
            <Tab label="1-1" />
            <Tab label="Retro da Tribo" />
            <Tab label="Próximos Eventos" />
          </Tabs>
          {/* <IconButton onClick={this.handleClick}>
            {this.state.play ? (
              <PauseButton style={{ color: '#ffffff' }} />
            ) : (
              <PlayButton style={{ color: '#ffffff' }} />
            )}
          </IconButton> */}
        </AppBar>
        {tabs === 0 && <Ritos />}
        {tabs === 1 && <Diagnostico />}
        {tabs === 2 && <OneOnOne />}
        {tabs === 3 && <Retro />}
        {tabs === 4 && <Eventos />}
      </MuiThemeProvider>
    );
  }
}

export default App;
